*,
*::after,
*::before {
  box-sizing: inherit; }

body {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  font-family: "Gotham Pro", "Arial", sans-serif;
  font-weight: 400;
  font-style: normal;
  font-size: 40px;
  color: #0d319c;
  background-color: white; }

a {
  text-decoration: none; }

img {
  max-width: 100%;
  height: auto; }

.visually-hidden {
  position: absolute;
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  margin: -1px; }

.container {
  margin: 0 auto; }

.page-main {
  position: relative;
  width: 1024px;
  height: 768px;
  margin: 0 auto;
  overflow: hidden; }

.main__container {
  width: 100%;
  height: 100%;
  position: relative;
  display: flex;
  flex-direction: row;
  list-style: none;
  padding: 0;
  margin: 0 auto;
  transition: 1s; }

@-webkit-keyframes bounce-big-circle {
  50% {
    transform: scale(1.2); }
  100% {
    transform: scale(1); } }

@keyframes bounce-big-circle {
  50% {
    transform: scale(1.2); }
  100% {
    transform: scale(1); } }

@-webkit-keyframes bounce-middle-circle {
  50% {
    transform: scale(1.5); }
  100% {
    transform: scale(1); } }

@keyframes bounce-middle-circle {
  50% {
    transform: scale(1.5); }
  100% {
    transform: scale(1); } }

@-webkit-keyframes bounce-small-circle {
  50% {
    transform: scale(2); }
  100% {
    transform: scale(0.5); } }

@keyframes bounce-small-circle {
  50% {
    transform: scale(2); }
  100% {
    transform: scale(0.5); } }

.targets {
  position: absolute;
  top: 0;
  left: 0;
  width: 1024px;
  height: 768px;
  padding: 100px 70px 0 70px;
  background: url("../img/bg-1.jpg") no-repeat center;
  background-size: contain;
  overflow: hidden; }

.targets__title {
  font-family: "Gotham Pro", Arial, sans-serif;
  font-weight: 400;
  font-size: 50px;
  margin: 0;
  padding-right: 100px; }

.targets__list {
  position: relative;
  list-style: none;
  padding: 0;
  margin: 0;
  width: 100%;
  min-height: 490px; }

.targets__item {
  position: absolute;
  font-family: "Lato", Arial, sans-serif;
  font-weight: 400;
  font-size: 20px; }

.targets__item:nth-child(1) {
  top: 57px;
  right: 185px; }

.targets__item:nth-child(2) {
  top: 175px;
  left: 152px; }

.targets__item:nth-child(3) {
  bottom: 180px;
  left: 335px; }

.targets__item:nth-child(4) {
  bottom: 200px;
  right: 80px; }

.targets__item-marker {
  display: block;
  position: absolute;
  width: 45px;
  height: 45px;
  border: 2px #de791b solid;
  border-radius: 50%;
  background-color: transparent;
  -webkit-animation: 2s bounce-big-circle 2s linear infinite;
          animation: 2s bounce-big-circle 2s linear infinite; }

.targets__item-marker::before {
  display: block;
  content: "";
  position: absolute;
  top: 5px;
  left: 5px;
  width: 30px;
  height: 30px;
  border: 2px #de791b solid;
  border-radius: 50%;
  background-color: #de791b;
  opacity: 0.6;
  -webkit-animation: 2s bounce-middle-circle 1s linear infinite;
          animation: 2s bounce-middle-circle 1s linear infinite; }

.targets__item-marker::after {
  display: block;
  content: "";
  position: absolute;
  top: 13px;
  left: 13px;
  width: 15px;
  height: 15px;
  border-radius: 50%;
  background-color: #de791b;
  opacity: 0.6;
  -webkit-animation: 2s bounce-small-circle 0s linear infinite;
          animation: 2s bounce-small-circle 0s linear infinite; }

.targets__item-marker--small {
  width: 25px;
  height: 25px; }

.targets__item-marker--small::before {
  top: 3px;
  left: 3px;
  width: 15px;
  height: 15px; }

.targets__item-marker--small::after {
  top: 7px;
  left: 7px;
  width: 7px;
  height: 7px; }

.targets__item-marker--big {
  width: 60px;
  height: 60px; }

.targets__item-marker--big::before {
  top: 8px;
  left: 8px;
  width: 40px;
  height: 40px; }

.targets__item-marker--big::after {
  top: 20px;
  left: 20px;
  width: 15px;
  height: 15px; }

.targets__item:nth-child(1) .targets__item-marker {
  top: -10px;
  left: -68px; }

.targets__item:nth-child(2) .targets__item-marker {
  bottom: -48px;
  left: 42px; }

.targets__item:nth-child(3) .targets__item-marker {
  bottom: -29px;
  left: 75px; }

.targets__item:nth-child(4) .targets__item-marker {
  bottom: -28px;
  left: 36px; }

.targets__btn {
  position: relative;
  display: block;
  border: none;
  background: none;
  padding: 0;
  margin: 0 auto;
  font-family: "Gotham Pro", Arial, sans-serif;
  font-weight: 400;
  font-size: 15px;
  color: #ffffff;
  z-index: 1;
  outline: none; }

.targets__btn::before {
  content: "";
  display: block;
  position: absolute;
  width: 35px;
  height: 35px;
  border: 3px #ffffff solid;
  border-left: none;
  border-top: none;
  transform: rotate(45deg);
  left: 35px;
  bottom: -26px; }

.targets__btn::after {
  z-index: -1;
  content: "";
  display: block;
  position: absolute;
  width: 700px;
  height: 120px;
  border-radius: 50%;
  left: -295px;
  bottom: -155px;
  box-shadow: 0 0 30px 30px #f78b1f;
  background-color: #f78b1f; }

.targets__btn--hide {
  display: none; }

